@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat&display=swap);

.btn {
  font-family: 'Roboto', 'Roboto Slab', serif;
}

.heading {
  font-family: 'Roboto Slab', sans-serif;
}

.hand-written {
  font-family: "Caveat", 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 24px !important;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.anim.form-check-input{
    border: none !important;
}

.form-switch .anim.form-check-input:checked {
  background-image: url(data:image/png;base64,) !important;
}
.form-switch .anim.form-check-input:checked:focus {
  background-image: url(data:image/png;base64,) !important;
}

.form-switch .anim.form-check-input:focus {
  background-image: url(data:image/png;base64,) !important;
}
.form-switch .anim.form-check-input {
  width: 4em !important;
  height: 2rem !important;
  background-image: url(data:image/png;base64,) !important;
}
.anim.form-check-input:focus {
  outline: 0 !important;
  box-shadow: none !important;
  border: #000 !important;
}
.anim.form-check-input:checked {
  background-color: #fff;
  border-color: #fff;
}
